import React from "react";
import "../../App.css";
import { BrowserRouter as Router, Link, Route, Switch } from "react-router-dom";
import MediaQuery from 'react-responsive';
import "../../mobile.css";

export default function MasterCard (props) {


  return (
    <div>
    <MediaQuery minDeviceWidth={769}>
    <div className="about-master">
      <div className="about-master-photo">
        <img
          src= {props.src}
          alt="master"
          style={{ borderRadius: "100%", width: "150px", height: "150px" }}
        />
        <h5>
          <i><a className="no-shadow" style={{background:"none"}} href= {props.insta}> {props.instaname} </a></i>
        </h5>
      </div>

      <div className="about-master-text">
        <h2 style={{ marginBottom: "5px" }}>{props.name}</h2>
        <h4 style={{ marginTop: "5px" }}>
          <i>{props.style} </i>
        </h4>
        <p style={{textAlign: "left"}}>
        {props.descr}
        </p>
      </div>
    </div>
    </MediaQuery>

    {/* Mobile version */}

    <MediaQuery maxDeviceWidth={768}>
    <div className="about-master-mobile">
      <div className="about-master-photo-mobile">
        <img
          src= {props.src}
          alt="master"
          style={{ borderRadius: "80%", width: "100px", height: "100px" }}
        /><div class="mobile-row">
          <img src="/images/insta.png"></img>
        <h5>
          <i><a className="no-shadow" style={{background:"none"}} href= {props.insta}> {props.instaname} </a></i>
        </h5>
        </div>
      </div>

      <div className="artist-mobile-text">
        <h2 style={{ marginBottom: "1px" }}>{props.name}</h2>
        <h4 style={{ marginTop: "1px" }}>
          {props.style}
        </h4>
        <p style={{textAlign: "left"}}>
        {props.descr}
        </p>
      </div>
    </div>
    <svg width="187" height="10" viewBox="0 0 187 7" fill="none" xmlns="http://www.w3.org/2000/svg">
<line y1="-1" x2="187" y2="-1" transform="matrix(-1 0 0 1 187 2)" stroke="#800080" stroke-width="2"/>
<path d="M157 6L2.38419e-07 5.99999" stroke="#800080" stroke-width="2"/>
</svg>

    </MediaQuery>
    </div>
  );
}
