import React from "react";
import "../App.css";
import MenuItem from "./MenuItem";
import { Redirect } from "react-router";

class MenuPage extends React.Component {
  constructor(props) {
    super(props);
    this.changeBg = this.changeBg.bind();
  }

  state = {
    img: this.props.img,
    where: this.props.where,
  };

  style = {
    background: this.state.img,
    alignItems: this.props.position
  };


  changeBg = () => {
    return this.setState({
      img: "red"
    });
  };

  render() {
    let where;
    if (this.state.where == "left"){
      where = { left: "30px"}
    } else {
      where = { right: "40px"}
    }

    let opposite
    if (this.state.where == "left"){
      opposite = "7%"
    } else {
      opposite = "70%"
    }

    let divStyle = {
      position:"absolute",
      top: '90%',
      left: opposite,
      color: "white",
      fontSize: "11px",
    }

    let link2

    if (this.props.link2 !== "") {
      link2 = <MenuItem title={this.props.title2} link={this.props.link2}/>;
    } else {
      link2 = "";
    }

    return (
      <div className="menuPage comp" style={this.style}>
      <div className="button-back" style={where}><a className="no-shadow" style={{width:"0", height:"0", background:"none"}} href="/"> <img src="/images/majak.png" style={{width: "60px", height: "60px"}}/></a> </div>
        <MenuItem
          title={this.props.title1}
          link={this.props.link1}
          onClick={this.changeBg}
        />
        {link2}
        <MenuItem title={this.props.title3} link={this.props.link3} />
        <MenuItem title={this.props.title4} link={this.props.link4} />
        <p style={divStyle}> Developed by <a href="https://agubskyy.com" className="no-shadow" style={{background:"none", color: "pink"}}> Alex Gubskyy </a> </p>
      </div>
    );
  }
}

export default MenuPage;
