import React from "react";
import FrontParts from "./FrontParts";
import "../App.css";
import { BrowserRouter as Router, Link, Route, Switch } from "react-router-dom";
import MenuPage from "./MenuPage";
import KotovPortfolio from "./KotovPortfolio";
import KotovSketches from "./KotovSketches";
import AboutKotov from "./AboutKotov";
// import Book from "./Book";
import Contact from "./Contact";


export default class LeftPart extends React.Component {
  render() {
    return (
      <section className="half comp" style={{borderRight:"2px white solid"}}>
        <Switch>
          <Route
            path="/painstudio"
            render={() => (
              <MenuPage
                className="menuPage"
                img="url('/images/skull_bg.jpg')"
                where="left"
                position="flex-end"
                title1="About"
                title2="News"
                title3="Team"
                title4="Contact"
                link1="/painstudio/about"
                link2="/painstudio/news"
                link3="/painstudio/masters"
                link4="/painstudio/book"
              />
            )}
          />
          <Route
            path="/"
            exact
            render={() => (
              <FrontParts
                className="frontParts"
                img="url('/images/kotov-front.gif')"
                link="./alexkotov"
              />
            )}
          />
          <Route
            path="/alexkotov"
            exact
            render={() => (
              <FrontParts
                className="frontParts"
                img="black"
                link="./alexkotov"
              />
            )}
          />
          <Route path="/alexkotov/portfolio" exact component={KotovPortfolio} />
          // <Route path="/alexkotov/sketches" exact component={KotovSketches} />
          <Route path="/alexkotov/about" exact component={AboutKotov} />
          <Route
            path="/alexkotov/book"
            exact
            // render={props => <Book initial="Alex Kotov" />}
              component={Contact}
          />
        </Switch>
      </section>
    );
  }
}
